@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@600;900&family=VT323&display=swap);
*,
*::before,
*::after {
    box-sizing: border-box;
}

:root {
    --ff-primary: 'Source Code Pro', monospace;
    --ff-secondary: 'VT323', monospace;

    --fw-reg: 600;
    --fw-bold: 900;

    --clr-light: #fff;
    --clr-dark: #191919;
    --clr-accent: #00ff33;
    --clr-disabled: #555;

    --fs-h1: 2rem;
    --fs-h4: 1.5rem;
    --fs-body: 1rem;

    --bs: 0.25em 0.25em 0.75em rgba(0,0,0,.25),
          0.125em 0.125em 0.25em rgba(0,0,0,.15);
}

body {
  background: #191919;
  background: var(--clr-dark);
  font-family: 'VT323', monospace;
  font-family: var(--ff-secondary);
}

/* Typography */

h1,
h4 {
    line-height: 1.1;
    margin: 0;
}

h1 { font-size: 2rem; font-size: var(--fs-h1) }
h4 { font-size: 1.5rem; font-size: var(--fs-h4) }

@media (min-width: 600px) {
  :root {
    --fs-h1: 2.5rem;
    --fs-h4: 1.75rem;
    --fs-body: 1.5rem;
  }
}

@media (min-width: 768px) {
  :root {
    --fs-h1: 3rem;
    --fs-h4: 2rem;
    --fs-body: 1.75rem;
  }
}

.app {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin: 10px;
  -webkit-align-items: center;
          align-items: center;
}

.title {
    margin-top: .5em;
    margin-bottom: .5em;
    font-weight: var(--fw-reg);
    color: var(--clr-accent);
}

.subtitle {
    margin-top: 1em;
    margin-bottom: 1em;
    font-weight: var(--fw-reg);
    color: var(--clr-accent);
}

.btn {
  background-color: var(--clr-accent);
  border: 0;
  width: 6em;
  height: 2.5em;
  -webkit-align-self: center;
          align-self: center;
  font-weight: var(--fw-reg);
  color: var(--clr-dark);
  font-family: var(--ff-secondary);
  font-size: var(--fs-body)
}

.inputArea {
  height: 10em;
  background-color: var(--clr-disabled);
  border: 0;
  padding: 0.75em;
  resize: none;
  font-size: var(--fs-body);
  font-family: var(--ff-secondary);
  width: 100%;
}

.inputArea:focus {
  outline: none;
}

.inputArea:enabled {
  background-color: var(--clr-accent);
}

@media (min-width: 600px) {
  .inputArea {
    max-width: 75%;
  }
}

@media (min-width: 768px) {
  .inputArea {
    max-width: 50%;
  }
}

